.gray_list h1 {
    margin-top: 5rem;
    font-size: 3rem;
    margin-left: 5rem;
 }

 .btns-new {
    display: grid;
    margin: auto;
    margin-left: 8rem;
    /* margin-top: 2rem; */
    grid-template-columns: repeat(2, 1fr);
 }

 .lighter {
    background-color: #BBB;
    height: 37vh;
    border-radius: 1.5rem;
    margin: 0.5rem;
    text-align: left;
    font-size: 1.4rem;
 }

 li {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    font-size: 2rem;
 }

 .grey_list input {
    width: 8rem;
    height: 1,5rem;
    margin: 1.5rem 3rem 1.5rem 0;
 }

 .lighter input {
    margin: 0.5rem;
 }

 label {
    margin: 0.5rem;
 }